import { createSlice } from "@reduxjs/toolkit";

export const lobbySlice = createSlice({
  name: "lobby",
  initialState: {
    size: 0,
    code: null,
    status: null,
    users: [],
  },
  reducers: {
    setUsers: (state, action) => {
      state.users = action.payload;
      return state;
    },
    setLobbyDetails: (state, action) => {
      state.code = action.payload.lobbyCode;
      state.size = action.payload.lobbySize;
      state.cats = action.payload.noOfCatsForCurrentLobby;
      state.status = action.payload.lobbyStatus;
      return state;
    },
  },
});

// Action creators are generated for each case reducer function
export const { setUsers, setLobbyDetails } = lobbySlice.actions;

export default lobbySlice.reducer;
