import * as React from "react";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import {
  Button,
  CardMedia,
  IconButton,
  Paper,
  Table,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from "@mui/material";
import { AccountCircle } from "@mui/icons-material";
import { useDispatch, useSelector } from "react-redux";
import { setMessage } from "../Redux/message.slice";

function Win({ setCurrentPage }) {
  let score = useSelector((state) => state.message.score);
  const dispatch = useDispatch();
  return (
    <>
      <Paper
        elevation={5}
        style={{ margin: "auto", maxWidth: "50%", textAlign: "center" }}
      >
        <TableContainer
          component={"paper"}
          style={{ margin: "auto", maxWidth: "50%" }}
        >
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Name</TableCell>
                <TableCell>Score</TableCell>
              </TableRow>
            </TableHead>
            {score
              // .sort((a, b) => (a.totalPoints > b.totalPoints ? 1 : -1))
              .map((k) => (
                <TableRow>
                  <TableCell>{k.userName}</TableCell>
                  <TableCell>{k.totalPoints}</TableCell>
                </TableRow>
              ))}
          </Table>
        </TableContainer>
      </Paper>
    </>
  );
}

export default Win;
