import Box from "@mui/material/Box";
import {
  Button,
  CircularProgress,
  Fab,
  Paper,
  Tooltip,
  Typography,
} from "@mui/material";
import { AccountCircle } from "@mui/icons-material";
import {
  setCards,
  setCurrentCard,
  setCurrentUser,
  setRecentCard,
} from "../../Redux/table.slice";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import axios from "axios";
import { setMessage } from "../../Redux/message.slice";
import { green, red } from "@mui/material/colors";

function GameTable({ setCurrentPage }) {
  const dispatch = useDispatch();
  const users = useSelector((state) =>
    state.lobby.users.filter((k) => k.userId !== state.user.userId)
  );
  const currentUser = useSelector(
    (state) => state.table.currentUser === state.user.userId
  );
  let code = useSelector((state) => state.lobby.code);

  const user = useSelector((state) => state.user.userId);
  const user_details = useSelector((state) => state.user);
  const cards = useSelector((state) => state.table.cards);
  const win = useSelector((state) => state.table.cards.length);
  const defaultCardWidth = 100 / (cards.length ?? 1);

  const recentCards = useSelector((state) => state.table.recentCards);
  useEffect(() => {
    // http get cards method
    axios
      .get(`/v1/getUserDetails?userId=${user}&lobbyId=${code}`)
      .then((res) => dispatch(setCards(res.data.cardList.sort())))
      .catch((err) => console.log(err));
  }, [dispatch]);

  // let x = (95 * window.innerWidth) / 100,
  //   y = (85 * window.innerHeight) / 100;
  // const radius = x < y ? x / 2 : y / 2;

  // let locations = [];
  // for (let i = 0; i < n; i++) {
  //   const xRect = Math.cos(((2 * Math.PI) / n) * i) * radius + radius;
  //   const yRect = Math.sin(((2 * Math.PI) / n) * i) * radius + radius;

  //   locations.push({ x: xRect, y: yRect });
  // }

  return (
    <Paper
      elevation={5}
      style={{
        margin: "auto",
        height: "95%",
        width: "95%",
        display: "flex",
        flexDirection: "column",
        backgroundImage: "url('/images/base.jpg')",
      }}
    >
      <Box display={"flex"} flex={1} flexDirection="column" margin={1}>
        <Box display={"flex"} justifyContent="center" alignItems={"center"}>
          {(users.length === 1 ? [users[0]] : users.slice(2)).map((k) => (
            <PlayerCard user={k} />
          ))}
        </Box>
        <Box display={"flex"} flex={1}>
          <Box display={"flex"} justifyContent="center" alignItems={"center"}>
            {users.length !== 1 && <PlayerCard user={users[0]} />}
          </Box>
          <Box
            flex={1}
            display={"flex"}
            justifyContent="center"
            height={"100%"}
            alignItems={"center"}
            gap={3}
          >
            {recentCards.map(
              (a, index) => (
                <>
                  {a.min.map((k, i) => (
                    <MiddleCards min={k} max={a.max[i]} index={index} />
                  ))}
                </>
              ),
              []
            )}
          </Box>
          <Box display={"flex"} justifyContent="center" alignItems={"center"}>
            {users.length !== 1 && <PlayerCard user={users[1]} />}
          </Box>
        </Box>
        <Box
          display={"flex"}
          justifyContent="center"
          flexDirection={"column"}
          alignItems={"center"}
        >
          <Box
            elevation={1}
            sx={{
              display: "flex",
              flex: 1,
              width: "100%",
              overflowX: "auto",
              justifyContent: "center",
              padding: 1,
              paddingTop: 3,
              backgroundColor: "transparent",
            }}
          >
            {cards.map((k) => {
              return <Patta k={k} defaultCardWidth={defaultCardWidth} />;
            })}
          </Box>
          <PlayerCard
            user={{
              userId: user_details.userId,
              userName: user_details.username,
            }}
          />
        </Box>
      </Box>
      <Box display={"flex"} justifyContent="center">
        <Box sx={{ margin: 1, display: "flex", gap: 3 }}>
          <Button
            variant="contained"
            color="error"
            onClick={() => setCurrentPage(1)}
          >
            Exit
          </Button>
          <Button
            variant="contained"
            onClick={() =>
              currentUser &&
              dispatch(
                setMessage({
                  msg: "FIND_NEXT_PLAYER",
                  requestBody: {
                    cardPlayed: null,
                    currentUserId: user,
                    noOfCardsRemainig: win,
                    lobbyJoinCode: code,
                  },
                })
              )
            }
          >
            Pass
          </Button>
        </Box>
      </Box>
    </Paper>
  );
}

const PlayerCard = ({ user }) => {
  const currentUser = useSelector((state) => {
    return state.table.currentUser === user?.userId;
  });
  const lastPass = useSelector((state) => {
    return state.table.lastPassedUser === user?.userId;
  });

  const buttonSx = {
    ...(currentUser && {
      bgcolor: green[500],
      "&:hover": {
        bgcolor: green[700],
      },
    }),
  };
  return (
    <Tooltip
      placement="top"
      PopperProps={{
        disablePortal: true,
      }}
      open={lastPass}
      disableFocusListener
      disableHoverListener
      disableTouchListener
      title={
        <>
          <Paper>
            <Typography variant="h6">Pass</Typography>
          </Paper>
        </>
      }
    >
      <Paper
        elevation={1}
        className={currentUser && "activeUser"}
        sx={{ width: "140px", margin: 2 }}
      >
        <Box sx={{ display: "flex", alignItems: "center", margin: 1 }}>
          <Box sx={{ mr: 1, position: "relative" }}>
            <Fab aria-label="save" color="primary" sx={buttonSx} size="small">
              <AccountCircle />
            </Fab>
            {currentUser && (
              <CircularProgress
                size={45}
                sx={{
                  color: red[500],
                  position: "absolute",
                  top: -2,
                  left: -2,
                  zIndex: 1,
                }}
              />
            )}
          </Box>
          <Typography flex={1} variant="h6" textAlign={"center"}>
            {user?.userName}
          </Typography>
        </Box>
      </Paper>
    </Tooltip>
  );
};

const Patta = ({ k, defaultCardWidth }) => {
  const currentUser = useSelector(
    (state) => state.table.currentUser === state.user.userId
  );
  let code = useSelector((state) => state.lobby.code);
  const user = useSelector((state) => state.user.userId);

  const currentCard = useSelector((state) => state.table.currentCard);
  const cardPlayable = useSelector((state) => state.table.cardPlayable);
  const win = useSelector((state) => state.table.cards.length);
  
  const dispatch = useDispatch();
  const onClick = () => {
    if (currentUser) {
      if (currentCard === k) {
        if (cardPlayable) {
          dispatch(
            setMessage({
              msg: "FIND_NEXT_PLAYER",
              requestBody: {
                cardPlayed: k,
                currentUserId: user,
                lobbyJoinCode: code,
                noOfCardsRemainig: win - 1,
              },
            })
          );
          dispatch(setRecentCard(k));
        }
        dispatch(setCurrentCard(null));
      } else {
        dispatch(setCurrentCard(k));
      }
    }
  };
  return (
    <Box
      height={"130px"}
      sx={{
        border: "1px solid gray",
        marginTop: currentCard === k ? "-20px" : "none",
      }}
      width={`${defaultCardWidth > 10 ? 10 : defaultCardWidth}%`}
    >
      <img
        alt={`${k}.png`}
        onClick={onClick}
        height={"100%"}
        width="100%"
        src={`/images/Patte/${k}.png`}
      />
    </Box>
  );
};

const MiddleCards = ({ min, max, index }) => {
  const recentCard = useSelector((state) => state.table.recentCard);

  return (
    <Box
      display={"flex"}
      justifyContent="center"
      height={"100%"}
      alignItems={"center"}
      width="10%"
    >
      <Box sx={{ position: "relative", height: "100%", width: `100%` }}>
        {min % 13 < 7 && (
          <Box
            height={"40%"}
            sx={{
              width: "100%",
              border: recentCard === min ? "2px solid red" : "1px solid gray",
              position: "absolute",
              top: "0%",
            }}
          >
            <img
              height={"100%"}
              alt={`${min}.png`}
              width="100%"
              src={`/images/Patte/${min}.png`}
            />
          </Box>
        )}
        {min && (
          <Box
            height={"40%"}
            sx={{
              width: "100%",
              border: max === min ? "2px solid red" : "1px solid gray",
              position: "absolute",
              top: "25%",
            }}
          >
            <img
              height={"100%"}
              alt={`${index * 13 + 7}.png`}
              width="100%"
              src={`/images/Patte/${index * 13 + 7}.png`}
            />
          </Box>
        )}
        {(max % 13 > 7 || max % 13 === 0) && (
          <Box
            height={"40%"}
            sx={{
              width: "100%",
              border: recentCard === max ? "2px solid red" : "1px solid gray",
              position: "absolute",
              top: "50%",
            }}
          >
            <img
              height={"100%"}
              alt={`${max}.png`}
              width="100%"
              src={`/images/Patte/${max}.png`}
            />
          </Box>
        )}
      </Box>
    </Box>
  );
};
export default GameTable;
