import * as React from "react";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import { Button, CardMedia, IconButton, Paper } from "@mui/material";
import { AccountCircle } from "@mui/icons-material";
import { useDispatch, useSelector } from "react-redux";
import { setMessage } from "../Redux/message.slice";

function LobbyPage({ setCurrentPage }) {
  let size = useSelector((state) => state.lobby.size);
  let users = useSelector((state) => state.lobby.users);
  let code = useSelector((state) => state.lobby.code);
  const dispatch = useDispatch();
  return (
    <>
      <Paper elevation={5} style={{ margin: "auto", maxWidth: "50%", textAlign: "center" }}>
        <Typography variant="h6" m={2}>Lobby Codee : {code}</Typography>
        <Paper
          sx={{
            display: "flex",
            flexWrap: "wrap",
            maxWidth: "100%",
            margin: "auto",
            justifyContent: "center",
            alignItems: "center",
            gap: 8,
            padding: 2,
          }}
        >
          {Array.from({ length: size }, (_, i) => i + 1).map((i, k) => (
            <Paper elevation={5} sx={{ height: "150px", width: "110px" }}>
              {users[k] ? (
                <Box sx={{ display: "flex", flexDirection: "column" }}>
                  <IconButton aria-label="play/pause" size="large">
                    <AccountCircle sx={{ height: 90, width: 90 }} />
                  </IconButton>
                  <Typography variant="h6" textAlign={"center"}>
                    {users[k]?.userName}
                  </Typography>
                </Box>
              ) : (
                <CardMedia
                  component="img"
                  sx={{
                    display: "flex",
                    height: "100%",
                    width: "100%",
                  }}
                  image="images/Patte/GC.png"
                  alt="Live from space album cover"
                />
              )}
            </Paper>
          ))}{" "}
        </Paper>
        <Box display={"flex"} justifyContent="center">
          <Button
            variant="text"
            onClick={() => setCurrentPage(1)}
            sx={{ margin: 3 }}
          >
            Cancel
          </Button>
          <Button
            variant="contained"
            onClick={() => {
              dispatch(
                setMessage({
                  msg: "CARD_DISTRIBUTE",
                  requestBody: {
                    lobbyId: code,
                  },
                })
              );
            }}
            sx={{ margin: 3 }}
          >
            Start Game
          </Button>
        </Box>
      </Paper>
    </>
  );
}

export default LobbyPage;
