import * as React from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import { Button, Card, TextField } from "@mui/material";
import { setLobbyDetails, setUsers } from "../Redux/lobby.slice";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { setMessage } from "../Redux/message.slice";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}
function LandingPage({ setCurrentPage, data, setData }) {
  const [value, setValue] = React.useState(0);
  const [error, setError] = React.useState(null);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const user = useSelector((state) => state.user.userId);

  const [lobbyCode, setLobbyCode] = React.useState(null);
  const [size, setSize] = React.useState(2);

  const dispatch = useDispatch();
  const onLobbyCreate = () => {
    if (size <= 1 || size >= 9) {
      setError(
        "Lobby size must greater than or equal to 2 and less than or equal to 8"
      );
    } else {
      axios
        .post("/v1/lobby/createlobby", {
          lobbyOwnerId: user,
          lobbySize: size,
        })
        .then((res) => {
          res = res.data;
          setError(null);
          dispatch(setLobbyDetails(res));
          dispatch(setUsers(res.listOfConnectedUsers));
          setCurrentPage(2);
        })
        .catch((err) => console.log(err));
    }
  };
  const handleSizeChange = (e) => {
    let val = parseInt(e.target.value);
    if (!val || val <= 1 || val >= 9) {
      setError(
        "Lobby size must greater than or equal to 2 and less than or equal to 8"
      );
      setSize(val);
    } else {
      setSize(val);
      setError(null);
    }
  };
  return (
    <>
      <Card sx={{ width: "30%", margin: "auto" }}>
        <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
          <Tabs value={value} onChange={handleChange}>
            <Tab label="Create Lobby" {...a11yProps(0)} />
            <Tab label="Join Lobby" {...a11yProps(1)} />
          </Tabs>
        </Box>
        <TabPanel value={value} index={0}>
          <Box
            sx={{
              display: "flex",
              //   width: "20%",
              flexDirection: "column",
              gap: 4,
            }}
          >
            <TextField
              label="Lobby Size"
              helperText={error ?? ""}
              error={error ? true : false}
              type={"number"}
              value={size}
              onChange={handleSizeChange}
            />
            <Button variant="contained" onClick={onLobbyCreate}>
              create lobby
            </Button>
          </Box>
        </TabPanel>
        <TabPanel value={value} index={1}>
          <Box
            sx={{
              display: "flex",
              //   width: "20%",
              flexDirection: "column",
              gap: 4,
            }}
          >
            <TextField
              label="Join Code"
              onChange={(e) => setLobbyCode(e.target.value)}
            />
            <Button
              variant="contained"
              onClick={() => {
                dispatch(setLobbyDetails({ lobbyCode }));
                dispatch(
                  setMessage({
                    msg: "JOIN_LOBBY",
                    requestBody: {
                      lobbyId: lobbyCode,
                      userId: user,
                    },
                  })
                );
              }}
            >
              Join Lobby
            </Button>
          </Box>
        </TabPanel>
      </Card>
    </>
  );
}

export default LandingPage;
